import React, { Component } from "react";
import { observer, inject } from "mobx-react";
import {
  PageHeader, Layout, Button, message, Modal, Alert, Spin, Select,
  Switch, Input, Menu, Dropdown, Checkbox, Tooltip,
} from "antd";
import { AdvanceTable, Icon, FieldSettingTable, Print, Image } from "@/components";
import { permissions, queryToObj, formatDate, tableName } from "@/components/utils/fun";
import Apis from "@/apis";
import { Resizable } from 'react-resizable';
import ModalEdit from '../modalEdit'
const { Content } = Layout;
const confirm = Modal.confirm;
let tableW = 180
const actionList = [{
  key: '0',
  title: '详情',
  action: 'findOne',
  type: 'link',
  iconName: 'ReadingMode'
}, {
  key: '1',
  title: '编辑',
  action: 'update',
  type: 'link',
  iconName: 'Edit'
}, {
  key: '2',
  title: '删除',
  action: 'delete',
  type: 'link',
  iconName: 'Delete'
}]
const toolsList = [{
  key: '0',
  title: '创建',
  iconName: 'Add',
  action: 'create'
}, {
  key: '1',
  title: '刷新',
  iconName: 'Refresh',
  action: 'refresh'
}, {
  key: '2',
  title: '批量导入',
  iconName: 'ExcelDocument',
  action: 'import'
}, {
  key: '3',
  title: '导出Excel',
  iconName: 'ExcelLogoInverse',
  action: 'export'
}, {
  key: '4',
  title: '打印',
  iconName: 'Print',
  action: 'print'
}, {
  key: '5',
  title: '查询',
  iconName: 'Zoom',
  action: 'query'
}]

const ResizableTitle = props => {
  const { onResize, width, ...restProps } = props;
  if (!width) {
    return <th {...restProps} />;
  }
  return (
    <Resizable
      width={width}
      height={0}
      handle={
        <span
          className="react-resizable-handle"
          onClick={e => {
            e.stopPropagation();
          }}
        />
      }
      onResize={onResize}
      draggableOpts={{ enableUserSelectHack: false }}
    >
      <th {...restProps} />
    </Resizable>
  );
};

@inject(({ globalStore, appStore, authStore }) => ({
  ...appStore,
  ...authStore,
  user: authStore.user,
  ...globalStore
}))
@observer
class ListView extends Component {
  tableRef = React.createRef();
  actions = []
  tableWidth = tableW
  oldTimestamp = 0
  state = {
    params: {},
    showAction: undefined,
    advancedSearch: true,
    columns: [],
    pageSize: 20,
    pageSettingId: undefined,
    tableUpdate: 0,
    showModal: false,
    modalTitle: '',
    modalId: '',
    scroll: { x: tableW },
    columnsMenu: [],
    viewType: 'edit'
  };

  async componentDidMount() {
    const { pageSettings = {}, requestPageSetting } = this.props;
    let pageSetting = pageSettings[window.location.hash] || await requestPageSetting()
    const { _id, showAction, pageSize, columns = [] } = pageSetting || {}
    this.setState({
      pageSettingId: _id,
      pageSize: pageSize || 20,
      showAction,
      columnsMenu: columns
    }, () => this.initColumns(this.props))
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { timestamp } = nextProps
    if (timestamp !== this.oldTimestamp || !timestamp) {
      this.oldTimestamp = timestamp
      this.initColumns(nextProps);
    }
  }

  initColumns(props) {
    const { action = "", columns = [], tbName } = props || {};
    let _columns = []
    this.actions = action.split(",") || [];
    tableW = actionList.filter(item => permissions(tbName, item.action)).length * 80
    this.tableWidth = permissions(tbName, 'update') ? tableW : 0

    if (Array.isArray(columns)) {
      _columns = columns.map((col, index) => {
        let settingObj = this.state.columnsMenu.find(item => item.id === col.id) || {}
        if (!settingObj.hidden) this.tableWidth += Number(col.width) || 150;
        let { id, render, title, isExtend, dataIndex, selectOptions, sortEnable, ...attr } = col;
        if (this.state.advancedSearch) {
          return {
            id,
            sorter: sortEnable ? true : false,
            dataIndex: !isExtend ? dataIndex : undefined,
            width: attr.width,
            ...settingObj,
            title,
            // filters: selectOptions ? strToArray(selectOptions) : false,
            children: [{
              key: index,
              dataIndex: !isExtend ? dataIndex : undefined,
              ...attr,
              ...settingObj,
              title: this.renderSearchInput(col),
              render: this.renderColumn.bind(this, col),
            }],
          };
        } else {
          return {
            id,
            key: index,
            title,
            sorter: sortEnable ? true : false,
            dataIndex: !isExtend ? dataIndex : undefined,
            ...attr,
            ...settingObj,
            render: this.renderColumn.bind(this, col),
          };
        }
      });
    }
    if (permissions(tbName, 'update')) {
      _columns.push({
        key: _columns.length + 1,
        action: 'oper',
        title: "操作",
        width: tableW,
        fixed: this.state.showAction ? 'right' : false,
        render: (row) => this.renderActions(row),
      });
    }
    // console.warn(_columns);
    this.setState({
      columns: _columns,
      columnsMenu: _columns.map(item => ({
        id: item.id,
        title: item.title,
        width: item.width,
        hidden: item.hidden
      })),
      scroll: { x: this.tableWidth },
    })
  }

  renderColumn = (col, val, row, index) => {
    const { execFun } = this.props
    let { render, isExtend, dataIndex, isPhoto, photoWidth, selectOptions } = col;
    if (render) {
      let value = dataIndex && !isExtend ? execFun(render, val, row, index) : execFun(render, null, row, index)
      if (isPhoto) {
        return (
          <Image src={value} photoWidth={photoWidth} />
        )
      } else {
        return value
      }
    } else {
      if (selectOptions) {
        return (selectOptions.options.find(item => item.value === val) || {}).key
      } else {
        return val
      }
    }
  }

  renderSearchInput = col => {
    if (col.forSearch) {
      switch (col.component) {
        case 'Select':
          let _options = col.selectOptions ? col.selectOptions.options.map(item => ({
            label: item.key,
            value: item.value
          })) : []
          return (
            <Select
              allowClear
              style={{ width: '100%' }}
              placeholder="选择"
              onChange={this.handleInput.bind(this, col)}
              options={_options}
            />
          )
        case 'Switch':
          return <Switch onChange={this.handleInput.bind(this, col)} />
        default:
          return (
            <Input
              placeholder="查询"
              onChange={this.handleInput.bind(this, col)}
              onPressEnter={() => this.setState({ tableUpdate: +new Date() })}
              allowClear
            />
          )
      }
    } else {
      return null
    }
  }

  renderActions = (row) => {
    const { tbName, } = this.props;
    return <>
      {actionList.map(item => (
        this.actions.includes(item.action) && permissions(tbName, item.action) ? (
          <Button type={item.type} size="small" icon={<Icon iconName={item.iconName} marginRight={5} />}
            onClick={this.handleAction.bind(this, item, row)} key={`btn_${item.key}`}>
            {item.title}
          </Button>
        ) : null
      ))}
    </>
  }

  renderTools = () => {
    return <>
      {
        toolsList.map(item => {
          return this.actions.includes(item.action) ? (
            item.action === 'print' ? (
              <Print content={this.tableRef} key={`tools_${item.key}`}>
                <Button
                  type="text"
                  size="small"
                  icon={<Icon iconName={item.iconName} marginRight={5} color="#007eff" />}
                > {item.title}</Button>
              </Print>
            ) : (
              <Button
                key={`tools_${item.key}`}
                type="text"
                size="small"
                icon={<Icon iconName={item.iconName} marginRight={5} color="#007eff" />}
                onClick={this.handleToolClick.bind(this, item)}
              > {item.title}</Button>
            )
          ) : null
        })
      }
    </>
  }

  handleAction = (item, row, e) => {
    e.stopPropagation();
    const { title, tbName, } = this.props
    if (item.action === 'delete') {
      confirm({
        title: `你确定要删除所选${title}吗?`,
        okText: "确定",
        cancelText: "取消",
        onOk: () => {
          Apis[`appRemove`]({ _id: row._id }, tableName(tbName)).then((res) => {
            if (res) {
              message.success("删除成功!");
              this.setState({ tableUpdate: +new Date() });
            } else {
              message.error("删除失败!");
            }
          });
        },
      });
    } else if (item.action === 'update') {
      this.setState({
        showModal: true,
        viewType: 'edit',
        modalTitle: `${item.title}${title}`,
        modalId: row._id
      })
    } else if (item.action === 'findOne') {
      this.setState({
        showModal: true,
        viewType: 'detail',
        modalTitle: `${title}${item.title}`,
        modalId: row._id
      })
    }
  };

  handleCheck = (item, e) => {
    e.stopPropagation();
    const { columns, columnsMenu } = this.state
    let hasOne = columns.find(val => val.id === item.id);
    if (hasOne) {
      let hasItem = columnsMenu.find(col => col.id === item.id);
      if (hasItem) hasItem.hidden = !hasItem.hidden;
      hasOne.hidden = !hasOne.hidden;
      this.setState({
        columns,
        columnsMenu
      });
    }
  };

  handleToolClick = (item, e) => {
    const { title, } = this.props
    if (item.action === 'create') {
      this.setState({
        showModal: true,
        viewType: 'edit',
        modalTitle: `${item.title}${title}`,
        modalId: ''
      })
    } else if (item.action === 'refresh') {
      this.setState({ tableUpdate: +new Date() })
    } else if (item.action === 'query') {
      this.setState({ tableUpdate: +new Date() })
    }
  };

  handleResize = index => (e, { size }) => {
    const nextColumns = [...this.state.columns];
    const { title, sorter, children, ...props } = nextColumns[index]
    let colObj = {
      title, sorter,
      ...props,
      width: size.width,
    }
    if (children) {
      colObj.children = [{
        ...children[0],
        width: size.width,
      }]
    }
    nextColumns[index] = colObj;
    this.setState({ columns: nextColumns });
  };

  tableComponents = {
    header: {
      cell: ResizableTitle,
    },
  };
  // 保存当前设置
  handleSave = () => {
    const { user = {}, } = this.props
    let { columns, showAction, pageSize, advancedSearch, pageSettingId, } = this.state
    let _columns = this.props.columns.map(item => {
      let hasOne = columns.find(col => col.id === item.id)
      if (hasOne) {
        return { id: hasOne.id, width: hasOne.width, hidden: !!hasOne.hidden, title: hasOne.title }
      } else {
        return { id: item.id, width: item.width, title: item.title, hidden: true }
      }
    })
    let data = {
      _id: pageSettingId,
      url: window.location.hash,
      pageSize,
      columns: _columns,
      showAction,
      advancedSearch,
      uid: user.id
    }
    Apis.putPageCustomizations(data).then(res => {
      if (res && !res.statusCode) {
        this.setState({ pageSettingId: res._id, columnsMenu: _columns })
        message.success("保存成功!");
      } else {
        message.error(res.message);
      }
    })
  }

  handleInput = (col, e) => {
    switch (col.component) {
      case 'Input':
        this.transformValue(col.dataIndex, e.target.value, 'string')
        break;
      case 'InputNumber':
        this.transformValue(col.dataIndex, e.target.value, 'number')
        break;
      case 'DatePicker':
        this.transformValue(col.dataIndex, e.target.value, 'date')
        break;
      case 'Select':
        this.transformValue(col.dataIndex, e, 'none')
        break;
      case 'Switch':
        this.transformValue(col.dataIndex, e, 'boolean')
        break;
      default:
        break;
    }
  }
  // 点击查询搜索
  transformValue = (name, value, type) => {
    let { params } = this.state
    const typeFuns = {
      'number': val => val !== undefined ? Number(val) : undefined,
      'date': val => val ? formatDate(val, true) : undefined,
      'boolean': val => val !== undefined ? !!val : undefined,
      'string': val => val !== undefined ? `${val}` : undefined,
      'none': val => val !== undefined ? val : undefined
    }
    for (let key of Object.keys(params)) {
      if (key.indexOf(name) === 0) delete params[key]
    }
    if (`${value}`.indexOf('=') === 0) {
      let arr = value.split(' ')
      params[`${name}_eq`] = typeFuns[type](arr[1])
    } else if (`${value}`.indexOf('>') === 0) {
      let arr = value.split(' ')
      params[`${name}_gt`] = typeFuns[type](arr[1])
    } else if (`${value}`.indexOf('<') === 0) {
      let arr = value.split(' ')
      params[`${name}_lt`] = typeFuns[type](arr[1])
    } else {
      params[`${name}`] = typeFuns[type](value) || undefined
    }
    this.setState({ params, })
  }

  render() {
    const { tbName, setting, urlQuery, pageSize, showSetting, editViewName, detailViewName, viewData, comName, onUpdate } = this.props;
    let { params, advancedSearch, showAction, columns, tableUpdate, showModal,
      modalTitle, modalId, scroll, columnsMenu, showDrawer, viewType } = this.state;
    const _columns = columns.map((col, index) => ({
      ...col,
      onHeaderCell: column => ({
        width: column.width,
        onResize: this.handleResize(index),
      }),
    }));
    const menu = (
      <Menu style={{ width: 150 }}>
        {columnsMenu.map((item, index) => (
          <Menu.Item key={index}>
            <Checkbox onClick={this.handleCheck.bind(this, item)} checked={!item.hidden} >
              {item.title}
            </Checkbox>
          </Menu.Item>
        ))}
      </Menu>
    );
    return tbName ? (
      <>
        {permissions(tbName, 'find') ? (
          <>
            <PageHeader
              ghost={true}
              onBack={false}
              className="page-title"
              title={this.renderTools()}
              extra={[
                <Input.Group compact key="0">
                  {permissions(tbName, 'update') ? <Tooltip placement="bottom" title={showAction ? '取消固定操作栏' : '固定操作栏'} key="1">
                    <Button type="default" size="middle" icon={<Icon iconName={showAction ? 'ClosePane' : 'OpenPane'} />}
                      onClick={() => this.setState({ showAction: !showAction }, () => this.initColumns(this.props))} />
                  </Tooltip> : null}
                  {showSetting ? <Dropdown overlay={menu} key="2">
                    <Button type="default" size="middle" icon={<Icon iconName="Hide3" />} />
                  </Dropdown> : null}
                  {<Tooltip placement="bottom" title='表格列设置' key="4">
                    <Button type="default" size="middle" icon={<Icon iconName='Settings' />} onClick={() => this.setState({ showDrawer: true })} />
                  </Tooltip>}
                  {<Tooltip placement="bottomRight" title='保存当前表格设置' key="3">
                    <Button type="default" size="middle" icon={<Icon iconName='save' />}
                      onClick={this.handleSave} />
                  </Tooltip>}
                </Input.Group>
              ]}
            />
            <Content>
              <div className="app-content">
                {_columns.length ? (
                  <AdvanceTable
                    ref={this.tableRef}
                    columns={_columns}
                    pageSize={this.state.pageSize || pageSize || setting.pageSize}
                    size="small"
                    params={{
                      _sort: `createdAt:desc`,
                      ...queryToObj(urlQuery),
                      ...params,
                    }}
                    showSearch={advancedSearch}
                    rowKey={record => record._id}
                    api={`appList`}
                    totalApi={'appCount'}
                    tbName={tableName(tbName)}
                    scroll={scroll}
                    sticky
                    bordered
                    update={tableUpdate}
                    onChangePage={(...argus) => this.setState({ pageSize: argus[1] })}
                    components={this.tableComponents}
                    showHeader
                  />
                ) : null}
              </div>
            </Content>
            {
              showModal ?
                <ModalEdit
                  visible={showModal}
                  title={<span><Icon iconName="OpenEnrollment" size={16} marginRight={5} />{modalTitle}</span>}
                  onCancel={() => this.setState({ showModal: false })}
                  width={1100}
                  view={viewType === 'detail' ? detailViewName : editViewName}
                  viewType={viewType}
                  contentId={modalId}
                  footer={false}
                  bodyStyle={{ padding: '20px 0px' }}
                  destroyOnClose
                  centered
                  onChange={() => this.setState({ tableUpdate: +new Date() })}
                /> : null
            }
            {
              this.props.columns.length && showDrawer ?
                <FieldSettingTable
                  title="表格列设置"
                  onClose={e => this.setState({ showDrawer: false })}
                  visible={showDrawer}
                  viewData={viewData}
                  comName={comName}
                  onUpdate={onUpdate}
                  items={this.props.columns.filter(item => item.action !== 'oper')}
                /> : null
            }
          </>
        ) : (
          <Alert
            message="信息提示"
            description="没有权限查看内容, 请联系管理员."
            type="warning"
            showIcon
            style={{ margin: "20px" }}
          />
        )}
      </>
    ) : (
      <div className="app-spin">
        <Spin />
      </div>
    );
  }
}

export default ListView;
