import React from "react";
import { Modal, Button, Tooltip } from 'antd';
import Draggable from 'react-draggable';
import { observer, inject } from 'mobx-react';
import { Spin } from 'antd';
import * as comMap from '@components';
import { firstToUpper, filterObj } from '@components/utils/fun'
import Apis from '@/apis'

@inject(({ globalStore, appStore }) => ({
  setting: globalStore.setting,
  ...appStore
}))
@observer
class App extends React.Component {
  draggleRef = React.createRef();
  state = {
    disabled: true,
    bounds: { left: 0, top: 0, bottom: 0, right: 0 },
    title: '',
    name: '',
    tbName: '',
    pageLayout: '',
    moduleName: '',
    components: [],
    showDrawer: false,
    showFunDrawer: false,
    _showDrawer: false,
    _showFunDrawer: false,
    visible: false,
    viewData: {},
    timestamp: 0
  };

  componentDidMount() {
    this.initData()
  }

  initData = async (currentView) => {
    const { view, visible } = this.props
    currentView = currentView || view
    if (currentView) {
      let res = await Apis.viewList({ name: currentView, })
      if (Array.isArray(res) && res.length) {
        let { title, name, tbName, components, pageLayout, moduleName } = res[0]
        this.setState({
          title, name, tbName, pageLayout, components, moduleName, visible,
          viewData: res[0],
          timestamp: +new Date()
        })
      }
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { visible, view } = nextProps
    if (visible) {
      this.initData(view)
      this.setState({ visible })
    }
  }

  onStart = (event, uiData) => {
    const { clientWidth, } = window.document.documentElement;
    const targetRect = this.draggleRef.current.getBoundingClientRect();
    this.setState({
      bounds: {
        left: -targetRect.left + uiData.x,
        right: clientWidth - (targetRect.right - uiData.x),
        top: -targetRect.top + uiData.y,
        // bottom: clientHeight - (targetRect.bottom - uiData.y),
      },
    });
  };

  handleClose = e => {
    // e.stopPropagation();
    const { onCancel } = this.props
    if (onCancel) onCancel()
    this.setState({ visible: false })
  }

  renderBtns() {
    const { viewType } = this.props
    return (
      <span style={{ float: 'right', }}>
        {
          viewType === 'edit' ?
            <>
              <Tooltip placement="bottom" title='表单查表自填函数'>
                <Button type="text" onClick={() => this.setState({ showFunDrawer: true })} style={{ marginRight: 5 }} icon={
                  <comMap.Icon iconName="Variable" color="#fff" />
                }></Button>
              </Tooltip>
              <Tooltip placement="bottom" title='表单项设置'>
                <Button type="text" onClick={() => this.setState({ showDrawer: true })} style={{ marginRight: 5 }} icon={
                  <comMap.Icon iconName="Settings" color="#fff" />
                }></Button>
              </Tooltip>
            </> : null
        }
        <Button type="text" onClick={this.handleClose} icon={
          <comMap.Icon iconName="ChromeClose" color="#fff" />
        }></Button>
      </span>
    )
  }

  render() {
    const { bounds, disabled, components, tbName, pageLayout, moduleName,
      showDrawer, showFunDrawer, visible, viewData, timestamp } = this.state;
    const { title, pageSettings, contentId, onUpdate, comName, ...props } = this.props
    return (
      <>
        <Modal
          title={
            <div>
              {this.renderBtns()}
              <div
                style={{ width: '100%', cursor: 'move', }}
                onMouseOver={() => {
                  if (disabled) {
                    this.setState({ disabled: false, });
                  }
                }}
                onMouseOut={() => {
                  this.setState({ disabled: true, });
                }}
                onFocus={() => { }}
                onBlur={() => { }}
              >
                {title}
              </div>
            </div>
          }
          closable={false}
          maskClosable={true}
          zIndex={900}
          {...props}
          visible={visible}
          modalRender={modal => (
            <Draggable
              disabled={disabled}
              bounds={bounds}
              onStart={(event, uiData) => this.onStart(event, uiData)}
            >
              <div ref={this.draggleRef}>{modal}</div>
            </Draggable>
          )}
        // closeIcon={}
        >
          {(Array.isArray(components) && components.length) ?
            <>
              {
                components.map((item, index) => {
                  let { __component, __v, id, _id, ...itemProps } = item
                  let Com = comMap[firstToUpper(__component.replace('view.', ''))]
                  return Com ? <Com
                    {...this.props}
                    tbName={tbName}
                    pageLayout={pageLayout}
                    moduleName={moduleName}
                    pageSetting={pageSettings[window.location.hash] || {}}
                    id={contentId}
                    {...filterObj(itemProps, val => val !== undefined || val !== null)}
                    key={`com_${index}`}
                  /> : null
                })
              }
            </>
            : <div className="app-spin"><Spin /></div>
          }
        </Modal>
        {
          showDrawer ?
            <comMap.FieldSettingForm
              title="表单项设置"
              onClose={e => this.setState({ showDrawer: false })}
              visible={showDrawer}
              viewData={viewData}
              comName={'view.editview'}
              onUpdate={this.initData}
              timestamp={timestamp}
            /> : null
        }
        {
          showFunDrawer ?
            <comMap.FieldSettingFormFun
              title="表单查表自填函数"
              onClose={e => this.setState({ showFunDrawer: false })}
              visible={showFunDrawer}
              viewData={viewData}
              timestamp={timestamp}
            /> : null
        }
      </>
    );
  }
}

export default App;